<template>
  <div class="forgetWrap">
      <header>
          <router-link to="/profileinfo">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>重置密码</div>
      </header>
      <div class="form">
          <section class="input_container phone_number">
              <input type="text" placeholder="账号" v-model="username">
          </section>
          <section class="input_container">
              <input type="text" placeholder="旧密码" v-model="oldpassword">
          </section>
          <section class="input_container">
              <input type="text" placeholder="请输入新密码" v-model="newpassword">
          </section>
          <section class="input_container">
              <input type="text" placeholder="确认密码" v-model="confirmpassword">
          </section>
          <section class="input_container">
              <input type="text" placeholder="验证码" v-model="captcha_code">
              <div class="img_change_img">
                  <img :src="capUrl" alt="">
                  <div class="change_img">
                      <p>看不清</p>
                      <p class="p1" @click="changeCap">换一张</p>
                  </div>
              </div>
          </section>
      </div>
      <div class="login_container" @click="changePwd">确认修改</div>
      <section class="tip_text_container" v-if="isShow">
          <div class="tip_icon">
              <span class="s1"></span>
              <span class="s2"></span>
          </div>
          <p class="tip_text">密码修改成功</p>
          <div class="confrim" @click="surechange">确认</div>
      </section>
      <section class="tip_text_container" v-if="isShow1">
          <div class="tip_icon">
              <span class="s1"></span>
              <span class="s2"></span>
          </div>
          <p class="tip_text">密码不正确</p>
          <div class="confrim" @click="notchange1">确认</div>
      </section>
      <section class="tip_text_container" v-if="isShow2">
          <div class="tip_icon">
              <span class="s1"></span>
              <span class="s2"></span>
          </div>
          <p class="tip_text">两次密码不一致</p>
          <div class="confrim" @click="notchange2">确认</div>
      </section>
      <section class="tip_text_container" v-if="isShow3">
          <div class="tip_icon">
              <span class="s1"></span>
              <span class="s2"></span>
          </div>
          <p class="tip_text">验证码不正确</p>
          <div class="confrim" @click="notchange3">确认</div>
      </section>
  </div>
</template>

<script>
export default {
name:'Forget',
data(){
    return{
        capUrl:"",
        username:'',
        oldpassword:'',
        newpassword:'',
        confirmpassword:'',
        captcha_code:'',
        isShow:false,
        isShow1:false,
        isShow2:false,
        isShow3:false
    }
},
created(){
    this.requestCode();
},
methods:{
    changePwd(){
        this.axios.post("/v2/changepassword",{
            username:this.username,
            oldpassWord:this.oldpassword,
            newpassword:this.newpassword,
            confirmpassword:this.confirmpassword,
            captcha_code:this.captcha_code,
        }).then(res=>{
            console.log(res)
            if (res.success == '密码修改成功') {
                this.isShow = true;
            }else if (res.message == '密码不正确') {
                this.isShow1 = true;
            }else if (res.message == '两次密码不一致') {
                this.isShow2 = true;
            }else{
                this.isShow3 = true;
            }
        }).catch(err => {})
    },
    requestCode(){
        this.axios.post("/v1/captchas").then(res=>{
        this.capUrl = res.code;
    }).catch(err => {})
    },
    changeCap(){
        this.requestCode();
    },
    surechange(){
        this.isShow = false;
    },
    notchange1(){
        this.isShow1 = false;
    },
    notchange2(){
        this.isShow2 = false;
    },
    notchange3(){
        this.isShow3 = false;
    },
}
}
</script>

<style scoped>
.p1{
    color: #3b95e9;
    margin-top: .2rem;
}
.change_img{
    display: flex;
    flex-wrap: wrap;
    width: 2rem;
    justify-content: center;
}
.change_img>p{
    font-size: .55rem;
    color: #666;
}
.img_change_img{
    display: flex;
    align-items: center;
}
.img_change_img>img{
    width: 3.5rem;
    height: 1.5rem;
    margin-right: .2rem;
}
.input_container>input{
    font-size: .7rem;
    color: #666;
}
.forgetWrap{
    padding-top: 1.95rem;
}
a{
    text-decoration: none;
}
header>a{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
.form{
    background-color: #fff;
    margin-top: .6rem;
}
.login_container{
    margin: 1rem .5rem;
    font-size: .7rem;
    color: #fff;
    background-color: #4cd964;
    padding: .5rem 0;
    border: 1px;
    border-radius: .15rem;
    text-align: center;
}
.input_container{
    display: flex;
    justify-content: space-between;
    padding: .6rem .8rem;
    border-bottom: 1px solid #f1f1f1;
}
.input_container>input{
    outline: none;
    font-size: .7rem;
    color: #666;
    padding: 0;
    border: 0;
}
.phone_number{
    padding: .3rem .8rem;
}
.tip_text_container{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -6rem;
    margin-left: -6rem;
    width: 12rem;
    animation: tipMove .4s;
    background-color: #fff;
    padding-top: .6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px;
    border-radius: .25rem;
}
.confrim{
    font-size: .8rem;
    color: #fff;
    font-weight: 700;
    margin-top: .8rem;
    background-color: #4cd964;
    width: 100%;
    text-align: center;
    line-height: 1.8rem;
    border: 1px;
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
}
.tip_text{
    font-size: .7rem;
    color: #333;
    line-height: .9rem;
    text-align: center;
    margin-top: .8rem;
    padding: 0 .4rem;
}
.tip_icon{
    width: 3rem;
    height: 3rem;
    border: .15rem solid #f8cb86;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.s1{
    width: .12rem;
    height: 1.5rem;
    background-color: #f8cb86;
}
.s2{
    width: .2rem;
    height: .2rem;
    border: 1px;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f8cb86;
}
</style>